.header {
  min-height: 876px;
  padding: 0 calc(50% - 706px);
  padding-bottom: 2px;
  // margin-bottom: 112px;
  background-color: #1b2d37;
  background-image: url("../../img/bg_ripples.svg");
  background-position: 50% 0;
  background-repeat: no-repeat;
  background-size: cover;
}

.header__top {
  display: flex;
  justify-content: space-between;
  padding-top: 30px;
  margin-bottom: 225px;
}

.header__logo {
  display: flex;
}

.header__logo-img {
  margin-top: 3px;
  margin-right: 15px;
}

.header__logo-content {
  width: 154px;
}

.header__logo-content h3 {
  margin: 0;
  margin-top: 9px;
  font-family: $roboto-condensed;
  font-size: 25px;
  font-weight: bold;
  line-height: 29px;
  color: $white;
  text-transform: capitalize;
}

.header__logo-content p {
  margin: 0;
  padding-left: 2px;
  font-family: $roboto;
  font-size: 12px;
  line-height: 14px;
  color: #788891;
}


.header__contacts-menu {
  display: flex;
}

.header__contacts-list {
  display: flex;
  align-items: center;
  list-style: none;
  margin: 0;
  padding: 0;
}

.header__contacts-item {
  margin-right: 51px;
}

.header__contacts-link {
  padding-left: 34px;
  font-family: $ptmono;
  font-size: 16px;
  line-height: 18px;
  color: $white;
  text-decoration: none;

  span {
    display: inline-block;
    padding-left: 10px;
    margin-bottom: 3px;
  }
}

.header__btn {
  width: 180px;
  height: 45px;
  margin-top: 6px;
  border: 1px solid $white;
  border-radius: 5px;
}

.header__main {
  position: relative;
  width: 38%;
  margin-right: auto;

  &::before {
    position: absolute;
    top: -103px;
    right: -955px;
    width: 700px;
    height: 630px;
    background-image: url("../../img/header__img.png"), url("../../img/bg_triangles.svg");
    background-position: -7px 47px, 85.6% 84%;
    background-repeat: no-repeat;
    content: "";
  }
}

.header__main h1 {
  width: 778px;
  margin: 0;
  margin-left: -6px;
  margin-bottom: 28px;
  font-family: $roboto-condensed;
  font-size: 80px;
  font-weight: bold;
  line-height: 94px;
  color: $white;
  text-transform: uppercase;
  letter-spacing: -4.5px;
}

.header__main p {
  position: relative;
  width: 645px;
  margin: 0;
  margin-bottom: 44px;
  padding-left: 94px;
  font-family: $ptmono;
  font-size: 22px;
  font-weight: normal;
  line-height: 34px;
  color: $white;

  &::before {
    position: absolute;
    left: 0;
    top: 17px;
    width: 66px;
    height: 2px;
    background-color: $white;
    content: "";
  }
}

.header__main-btn {
  width: 407px;
  height: 55px;
  margin-left: 93px;
  font-size: 16px;
  line-height: 19px;
  background-color: $blue;
  border: none;
  border-radius: 5px;
}

.header__main b {
  display: block;
  width: 93px;
  margin-top: 130px;
  margin-left: -39px;
  font-family: $ptmono;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  color: rgba($white, 0.5%);
  transform: rotate(-90deg);
}

//tablet
@media (max-width: $tablet-width) {
  .header {
    min-height: 686px;
    padding: 0 calc(50% - 359px);
    margin-bottom: 54px;
  }

  .header__top {
    padding-top: 16px;
    margin-bottom: 111px;
  }

  .header__btn {
    display: none;
  }

  .header__contacts-item {
    margin-right: 0;
  }

  .header__contacts-link svg {
    width: 23px;
    height: 24px;
  }

  .header__contacts-link span {
    display: none;
  }

  .header__main {
    margin-left: 20px;

    &::before {
      right: -450px;
      width: 320px;
      background-position: 64px 29px, 68.5% 115%;
      background-size: 462px 341px, 1506px 910px;
    }
  }

  .header__main h1 {
    width: 330px;
    margin-left: 42px;
    margin-bottom: 31px;
    font-size: 60px;
    line-height: 70px;
  }

  .header__main p {
    width: 330px;
    padding-left: 48px;
    margin-left: 46px;
    margin-bottom: 55px;
    font-size: 18px;
    line-height: 28px;

    &::before {
      width: 33px;
    }
  }

  .header__main-btn {
    width: 379px;
  }

  .header__main b {
    display: none;
  }
}
