.popap-worm {
  display: none;
  width: 490px;
  padding: 33px 39px;
  background-color: $blue;
}

.popap-worm__wrapper {
  display: flex;
}

.popap-worm__title h3 {
  margin: 0;
  font-family: $roboto-condensed;
  font-weight: normal;
  font-size: 28px;
  line-height: 39px;
  color: $white;
}

.popap-worm__title p {
  margin: 0;
  font-family: $ptmono;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  color: $white;
}

.popap-worm input {
  width: 100%;
}

.popap-worm textarea {
  width: 100%;
}

.popap-worm button {
  width: 100%;
  margin-bottom: 40px;
}

.popap-worm label {
  position: relative;
  display: block;
  width: 300px;
  padding-left: 32px;
  font-family: $ptmono;
  font-weight: normal;
  font-size: 14px;
  line-height: 16px;
  color: rgba($white, 0.8%);

  &::before {
    position: absolute;
    top: 0;
    left: 0;
    width: 16px;
    height: 16px;
    border: 1px solid $white;
    content: "";
    box-sizing: border-box;
  }
}

.popap-worm input[type="checkbox"]:checked ~ label::after {
  position: absolute;
  top: 4px;
  left: 4px;
  width: 7.5px;
  height: 5px;
  border-left: 1px solid $white;
  border-bottom: 1px solid $white;
  content: "";
  transform: rotate(-45deg);
  box-sizing: border-box;
}
